const styles = {
  select: { margin: "0 20px" },
  buttonsCellContainer: {
    padding: "0 20px",
    width: "100%",
    height: "100%",
    display: "flex",
    justifyContent: "flex-end",
    alignItems: "center"
  },
  editButton: {
    background: "#f3f3f3",
    outline: "none",
    cursor: "pointer",
    padding: 4,
    display: "inline-flex",
    border: "none",
    borderRadius: "50%",
    boxShadow: "1px 1px 2px 0px rgb(0 0 0 / .3)"
  },
  buttonsCellEditorContainer: {
    height: "100%",
    width: "100%",
    display: "inline-flex",
    padding: "0 20px",
    justifyContent: "flex-end",
    alignItems: "center"
  },
  cancelButton: {
    background: "#f3f3f3",
    outline: "none",
    cursor: "pointer",
    marginRight: 10,
    padding: 2,
    display: "inline-flex",
    border: "none",
    borderRadius: "50%",
    boxShadow: "1px 1px 2px 0px rgb(0 0 0 / .3)"
  },
  saveButton: {
    background: "#f3f3f3",
    outline: "none",
    cursor: "pointer",
    padding: 2,
    display: "inline-flex",
    border: "none",
    borderRadius: "50%",
    boxShadow: "1px 1px 2px 0px rgb(0 0 0 / .3)"
  }
};

const handleSelect=(e)=>{
  const tok = 'test:c4777264-50ce-42f7-b70f-2c0bcea5d570';
const hash = btoa(tok);
const Basic = 'Basic ' + hash;
 // axios.post('https://api.365.systems/odata/v2/Enrollments(72c88005-30a9-4004-b941-3afaa785c1cb)/Complete', {headers : { 'Authorization' : Basic }})

  fetch('https://api.365.systems/odata/v2/Enrollments(72c88005-30a9-4004-b941-3afaa785c1cb)/Complete', {method:'POST', 
headers: {'Authorization': 'Basic ' + btoa(':c4777264-50ce-42f7-b70f-2c0bcea5d570')}})
.then(response => response.json())
.then(json => console.log(json));
}

const handleSelect1=(e)=>{
  const tok = ':c4777264-50ce-42f7-b70f-2c0bcea5d570';
const hash = btoa(tok);
const Basic = 'Basic ' + hash;

  fetch('https://api.365.systems/odata/v2/Enrollments(72c88005-30a9-4004-b941-3afaa785c1cb)/ResetProgress', {method:'POST', 
headers: {'Authorization': 'Basic ' + btoa(':c4777264-50ce-42f7-b70f-2c0bcea5d570')}})
.then(response => response.json())
.then(json => console.log(json));
}

const popover = (
  <Popover id="popover-basic">
    <Popover.Title as="h3">Popover right</Popover.Title>
    <Popover.Content>
      Update Successful
    </Popover.Content>
  </Popover>
);

const getColumns = ({ setRowsData }) => {
  return [
    {
      id: "1",
      field: "UserLoginName",
      label: "Name",
      cellRenderer: UsernameCell,
      editorCellRenderer: (props) => <UsernameCell {...props} isEdit />
    },
    {
      id: "2",
      field: "Roles",
      label: "Role"

    },
    {
      id: "3",
      field: "CoursePassingStatus",
      label: "Course Status"
    },
    {
      id: "4",
      field: "CompletionDate",
      label: "Completion Date"
    },
    {
      id: "buttons",
      width: "max-content",
      pinned: true,
      sortable: false,
      resizable: false,
      cellRenderer: ({
        tableManager,
        value,
        data,
        column,
        colIndex,
        rowIndex
      }) => (
        <div style={styles.buttonsCellContainer}>
          <Button variant="success" onClick={handleSelect}>Complete</Button>
        </div>
      )
    },
    {
      id: "buttons1",
      width: "max-content",
      pinned: true,
      sortable: false,
      resizable: false,
      cellRenderer: ({
        tableManager,
        value,
        data,
        column,
        colIndex,
        rowIndex
      }) => (
        <div style={styles.buttonsCellContainer}>
            <OverlayTrigger trigger="click" placement="right" overlay={popover}>

          <Button variant="danger" onClick={handleSelect1}>Reset</Button>
          </OverlayTrigger>
        </div>
      )
    }
  ];
};

export default getColumns;
